#pond {
  position: relative;
  display: flex;
  justify-content: center;
  background-image: url(https://raw.githubusercontent.com/BartoszSeno/ClickerZero/main/src/assets/MainImg/Pond/pond.gif);
  height: 970px;
  width: 1920px;
}

.BackToVillage {
  position: absolute;
  height: 150px;
  width: 800px;
  z-index: 1000;
}

#SellShop {
  margin-top: -1000px;
  display: flex;
  position: absolute;
  width: 500px;
  height: 970px;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  pointer-events: none;
}

.SellConteiner {
  background-repeat: no-repeat;
  height: 480px;
  width: 418px;
  display: flex;
  justify-content: center;
  pointer-events: all;
}

.Slider {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  margin-top: 215px;
  height: 250px;
}

.BU {
  background-image: url(https://raw.githubusercontent.com/BartoszSeno/ClickerZero/main/src/assets/MainImg/any/ButtonUp.png),
    url(https://raw.githubusercontent.com/BartoszSeno/ClickerZero/main/src/assets/MainImg/any/ButtonDown.png);
  background-repeat: no-repeat;
  margin-left: -40px;
}
.BU p {
  margin: 0;
  margin-top: -10px;
  transition: 0.35s;
}
.BU:active p {
  margin-top: 10px;
}
.BU:active {
  background-position-y: 10px, 0px;
}

.BU {
  padding-top: 5px;
  border-radius: 0;
  width: 300px;
  height: 80px;
  border: 4px solid transparent;
  background-color: transparent;
  cursor: pointer;
  font-size: 30px;
  font-family: "Alagard";
  transition: 0.35s;
  color: goldenrod;
  text-shadow: -3px 2px 0px rgba(0, 0, 0, 1), 2px 2px 0px rgba(0, 0, 0, 1),
    -3px -2px 0px rgba(0, 0, 0, 1), 2px -2px 0px rgba(0, 0, 0, 1);
}

.BS:hover {
  background-color: #624a2c;
  border: 4px solid #40311e;
}
